.menu_productos{
    
    max-width: 1366px;
    margin: auto;
    
}

.menu_productos_1{
    box-shadow: 2px 2px 5px #999;
 background-color: rgba(0,0,0,0.2);
 height: 60px;

 
}
.menu_productos_2{
    margin-top: 30px;
    background-color: rgba(0,0,0,0.2);
 
   }
   


.menu_productos ul{
    display: flex;
    justify-content: center;
    align-items: center;
}
.menu_productos a{
    margin: 20px;
    text-decoration: none;
   
}

.menu_productos li{
    list-style: none;
    
}

.menu_manejo_fluidos{
    display: flex;
    justify-content:flex-start;
    align-items: flex-start;
    text-align: start;
}

.menu_manejo_fluidos_1{
  
    background-color: rgba(0,0,0,0.1);
    width: 30%;
    margin-right: 20px;
    border-radius: 5px;
}
.menu_manejo_fluidos_2{
    width: 70%;
    background-color: rgba(0,0,0,0.1);
    border-radius: 5px;
}
.menu_manejo_fluidos ul {
  
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    line-height: 3.5em;
}



.bombas_diafragma img{
   width: 150px;
   height: 150px;
   object-fit: cover;
   border-radius: 5px;
   margin: 20px;
}

.bombas_diafragma{
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items:center ;

}
.bombas_piston img{
    width: 150px;
    height: 150px;
    object-fit: cover;
    margin: 20px;
    border-radius: 5px;
}

.bombas_piston{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}

.menu_construccion_1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-size: 1em;
    padding-top: 20px;
    box-shadow: 2px 2px 5px #999;
    
}


.menu_construccion_1 ul{
    
flex-wrap: wrap;
 justify-content: space-around;
 align-items: center;
 width: 100%;
}
.box_construccion {
display: block;
overflow: hidden;


}


@media(max-width:768px){
    .menu_manejo_fluidos{
        flex-direction: column;
    }

    .menu_manejo_fluidos_1{
     width: 100%;
    }
    .menu_manejo_fluidos_2{
        width: 100%;
     
    }
  
    
    .box_construccion {
    
        width: 50%;
       
        
      }

}