.header{
    width: 100%;
 
    
   
}



.header_flex{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0,0,0,0.4);
    height: 80px;
    padding: 20px;
    position: fixed;
    box-shadow: 2px 2px 5px #999;
    z-index: 1000;
}
.logo img{
    width: 170px;
    
}

.nav ul{
    display: flex;
    

}
.nav a{
    padding: 20px;
    text-decoration: none;
   
   
}

.nav li{

    list-style: none;
    
    
}


@media(max-width:768px){


/* --------------------Menu responsive------------------ */
.nav {
    position: absolute;
    top:80px;
    left:-100%;
    width: 200px;
    transition: all 0.4s;
    height: 100vh;
    background-color: rgba(0,0,0,0.4);
    list-style: none;
    
    box-shadow: 2px 2px 5px #999;
    line-height: 1.7em;
    
  
  }
.mostrar {
    left: 0;
  }
.opacar{
opacity: .1;
 height: 100%;

 width: 100%;

 

}

  nav ul{
      flex-direction: column;
      display: flex;
      padding: 0;
  }
.nav ul li {
    line-height: 4em;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    
  }
  .nav ul li:hover {
    line-height: 4em;
    background-color: rgba(0,0,0,0.1);
    
  }
  .nav ul li a {
    
  }

}
/* -----------------------Boton Menu Animado--------------------------------------- */

.menu-bar{
    width: 30px;
    cursor: pointer;
    
}

.uno, .dos, .tres{
    width: 100%;
    background: white;
    height: 3px;
    transition: all 0.5s;
    
}

.dos{
    margin: 5px 0;
}


.btn_x .dos{
    opacity:0;

}

.btn_x .uno{
    transform: rotate(45deg) translate(5px, 5px);
  
}
.btn_x .tres{
     transform: rotate(-45deg) translate(6px, -7px);
}

/* ------------------------------------------------------------------------ */

@media(min-width:768px){
    .menu-bar{
        display: none;
    }
}