.home_content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 550px;
    padding: 20px;
    line-height: 2.8em;
    color: rgba(0,0,0,0.4);
    font-size: 1.5em;
    line-height: 2.7em;
    color: rgba(0,0,0,0.4);
    transition: all 0.4s;
    background: url(../img/silver-background-5-Best-Wallpaper.png)no-repeat center center/cover;
    
}

.home_content h1{
      animation: animate1 2s forwards .1s ease-in-out; 
      opacity: 0;
  
  }
  @keyframes animate1{
        
      from{
      transform: translateX(100px);
      }
      
      to{
          opacity: 1;
          transform: translateX(0px);
          
        }
      }

.home_content_p{
    animation: animate2 3.5s forwards .1s ease-in-out; 
    opacity: 0;
   
}
@keyframes animate2{
      
    from{
    transform: translateY(100px);
    }
    
    to{
        opacity: 1;
        transform: translateY(0px);
        
      }
    }

.button_home{
    background-color: rgba(0,0,0,0.1);
    padding: 0px 20px;
    border-radius: 5px;
    margin-top: 20px;
   width: 170px;
  
    box-shadow: 2px 2px 5px #999;
    /* box-shadow:5px 5px 5px #ccc;
    box-shadow: 0 0 0.7142857142857143rem #cccccc; */
    cursor: pointer;
    text-align: center;
}
.button_home:hover{
    background-color: rgba(0,0,0,0.2);
  
}

.conocenos{
    background-color: rgba(0,0,0,0.2);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
.conocenos_1{
  width: 50%;
  background-color: rgba(0,0,0,0.1);
  border-radius: 5px;
  padding: 20px;
  font-size: 1.1em;
  line-height: 2em;
}
.conocenos_2{
  width: 50%;
 padding: 0px 20px 0px 20px;
}


